import clsx from "clsx";
import React, { memo } from "react";
import { Handle, ReactFlowProvider } from "react-flow-renderer";

import "./index.css";
import { useSelector } from "react-redux";

const CustomNode = memo((node) => {
  const isDestination = useSelector((state) => {
    const { activeGraphId } = state.ui;
    switch (activeGraphId) {
      case 1:
        return state.v1Graph.destinationNodeId === node.id;
      case 2:
        console.log(state.v2Graph);
        return state.v2Graph.destinationNodeId === node.id;
    }
  });

  const isSource = useSelector((state) => {
    const { activeGraphId } = state.ui;
    switch (activeGraphId) {
      case 1:
        return state.v1Graph.sourceNodeId === node.id;
      case 2:
        return state.v2Graph.sourceNodeId === node.id;
    }
  });

  return (
    <ReactFlowProvider>
      <div
        className={clsx(
          "node",
          isDestination && "destination",
          isSource && "source",
          node.data.shortestPath !== undefined && "visited"
        )}
      >
        <div className="label">
          {node.id}
          {node.data.shortestPath !== undefined && (
            <div className="shortest-path">{node.data.shortestPath}</div>
          )}
        </div>
        <Handle position="top" id="handle-top" />
        <Handle position="left" id="handle-left" />
        <Handle position="bottom" id="handle-bottom" />
        <Handle position="right" id="handle-right" />
      </div>
    </ReactFlowProvider>
  );
});

export default CustomNode;
