const selectedGraph = pentagon;

const v3Graph = createSlice({
  name: "v3Graph",
  initialState: selectedGraph,
  reducers: {
    reset: (state, action) => {
      return selectedGraph;
    },
    visit: (state, action) => {
      const { nodeId } = action.payload;
      const node = getNode(nodeId, state);
      const [shortestPath, parentId] = getShortestPath(nodeId, state);
      node.data = { shortestPath, parentId };
    },
    highlightPath: (state, action) => {
      let { nodeId: currentNodeId } = action.payload;
      let currentNode = getNode(currentNodeId, state);
      let parentNodeId = currentNode.data.parentId;
      while (parentNodeId !== "0") {
        const index = state.edges.findIndex(
          (edge) =>
            edge.source === parentNodeId && edge.target === currentNodeId
        );
        // console.log(
        //   `found edge ${parentNodeId} -> ${currentNodeId} at ${index}`
        // );
        state.edges[index].data.isHighlighted = true;
        console.dir(JSON.stringify(state.edges[index]));
        const parentNode = getNode(parentNodeId, state);
        currentNodeId = parentNodeId;
        parentNodeId = parentNode.data.parentId;
      }
    },
    clearHighlightedPath: (state, action) => {
      for (const [index, edge] of state.edges.entries()) {
        state.edges[index].data.isHighlighted = false;
      }
    },
  },
});

export const actions = {
  reset: () => (dispatch) => dispatch(v3Graph.actions.reset()),
  select:
    ({ nodeId }) =>
    (dispatch, getState) => {
      const { v3Graph: graphState } = getState();
      dispatch(v3Graph.actions.clearHighlightedPath());
      dispatch(v3Graph.actions.visit({ nodeId }));
      dispatch(v3Graph.actions.highlightPath({ nodeId }));
    },
};

export default v3Graph.reducer;
